.worksBox {
    width: 80vw;
    height: 80vh;
    position: relative;
    display: flex;
    justify-content: space-around;
    align-items: center; 
}

.works {
    width: 100vw;
    height: 100vh;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-image: linear-gradient(90deg, #b93d21, #222222);
    
}

.works h2{
    color: white;
    text-shadow: 1px 1px black;
}

@media (max-width: 1100px) {
  
    .worksBox {
        width: 80vw;
        
    }
    
    
    }

@media (max-width: 900px) {
    .worksBox {
        flex-direction: column;
        min-height: 110vh;
    }
    .works {
        min-height: 110vh;
    }
    }

    @media (max-width: 1200px) {
        .worksBox {
            flex-direction: column;
            min-height: 110vh;
        }
        .works {
            min-height: 110vh;
        }
        }