.containerCard {
  display: flex;
  width: 100%;
  height: 100%;
}

.arrowBack {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 5;
  top: 5%;
  left: 2%;
  width: 5rem;
  height: 2.5rem;
  border-radius: 10%;
  background-color: transparent;
  color: black;
  font-weight: bold;
  border: 2px solid #262626;
  z-index: 9;
  cursor: pointer;
  transition: ease-in-out 0.2s;
}

.arrowBack:hover {
  transform: scale(1.05);
  color: orangered;
  border: 2px solid orangered;
}

.workBox {
  width: 100vw;
  height: 75vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.imageBox {
  width: 50vw;
  height: 75vh;
  flex-direction: column;
  display: flex;
  align-items: center;
}

.imageBox {
  width: 50vw;
  height: 60vh;
  position: relative;
}

.weatherBig {
  width: 100%;
  max-width: 550px;
  height: auto;
  position: absolute;
  top: 0;
  left: 0;
}
.weatherSmall {
  width: 100%;
  max-width: 170px;
  position: absolute;
  bottom: 50%;
  right: 15%;
}

.infoBox {
  width: 30vw;
  height: 65vh;
  display: flex;
  flex-direction: column;
  text-shadow: 1px 1px#222222;
}

.infoBox h3 {
  text-shadow: 1px 1px#000;
  color: #f26c4f;
}

.goTo {
  display: block;
  margin-top: 2rem;
  width: 130px;
  line-height: 1.2rem;
  height: 45px;
  border: 1px solid #222222;
  background-color: transparent;
  text-transform: uppercase;
  font-weight: bold;
  text-shadow: 2px 2px #222222;
  box-shadow: 4px 4px 2px #0000008c;
  transition: ease-in-out 0.3s;
  color: #ffffff;
}

.goTo:hover {
  cursor: pointer;
  border: 3px solid #f26c4f;
  transform: scale(0.95);
  color: #dd4826;
  box-shadow: 2px 2px 2px #0000008c;
}

a {
  color: white;
  text-decoration: none;
}
.infoText {
  color: white;
}
@media (max-width: 2200px) {
  .workBox {
    width: 70vw;
  }
  .imageBox img:nth-child(1) {
    width: 95%;
    max-width: 600px;
    height: auto;
  }
  .imageBox img:nth-child(2) {
    width: 80%;
    max-width: 190px;
  }
}
@media (max-width: 1800px) {
  .imageBox img:nth-child(1) {
    width: 95%;
    max-width: 550px;
    height: auto;
  }
  .imageBox img:nth-child(2) {
    width: 80%;
    max-width: 170px;
  }
}

@media (max-width: 1350px) {
  .workBox {
    width: 90vw;
  }
  .imageBox img:nth-child(1) {
    width: 85%;
    max-width: 550px;
    height: auto;
  }
  .imageBox img:nth-child(2) {
    width: 80%;
    max-width: 150px;
  }
}

@media (max-width: 1080px) {
  .workBox {
    width: 100vw;
  }
  .imageBox img:nth-child(1) {
    width: 80%;
    max-width: 550px;
    height: auto;
  }
  .imageBox img:nth-child(2) {
    width: 80%;
    max-width: 130px;
  }
}
